/* Fonts */

@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@700&display=swap');

* {
  margin: 0;
  overflow: hidden;
}

.warn-block {
  display: none;
  pointer-events: none;
}

.App {
  background: #F4F9F9;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;
}

.block1 {
  width: 100%;
  height: 100vh;
  background: #2F2F2F;
  display: flex;
  justify-content: center;
  align-items: center;
}

.block2 {
  height: 100vh;
  width: 0%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
}

.App > .visual {
  position: absolute;
  width: 43%;
  
  filter: drop-shadow(50px 200px 30px rgba(0, 0, 0, 0.2));
  animation: rotate infinite linear 100s;
}

@keyframes rotate {
  from {transform: rotate(0deg);}
  to {transform: rotate(360deg);}
}

.abss {
  position: absolute;
  left: 0;
  width: 62%;
  transform: skewY(-5deg);
 
}

.bar1 {
  width: 72%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2.2vw 0vw 2.2vw 0vw;
  z-index: 2;
}

.bar2 {
  width: 28%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2.2vw 0vw 2.2vw 0vw;
  z-index: 2;
}

.login {
  color: black;
  font-family: 'Quicksand', sans-serif;
  font-size: 1.6vw;
  cursor: pointer;
  font-weight: bold;
}

.svg1 {
  width: 3vw;
  margin-left: 5vw;
}

.signup {
  color: white;
  font-family: 'Quicksand', sans-serif;
  font-size: 1.6vw;
  margin-right: 14vw;
  cursor: pointer;
}

.signup > div {
  display: inline-block;
}

.anchor1 {
  width: 30%;
  height: 16vh;
  padding: 1vw 1vw 0vw 0vw;
}

.anchor2 {
  width: 30%;
  height: 16vh;
  padding: 0vw 1vw 1vw 0vw;
  
  
}

.bottom-nav {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 72%;
  padding: 2.2vw 0vw 2.2vw 0vw;
  text-align: center;
}

.copy {
  color: white;
  font-family: 'Quicksand', sans-serif;
  font-size: 1vw;
}

.head1 {
  z-index: 11;
  color: white;
  font-family: 'Lato', sans-serif;
  position: absolute;
  left: 7.8vw;
  top: 11.6vw;
  
}

.head1 > div {
  display: inline-block;
  font-size: 6.6vw;
  font-weight: lighter;
}

.underline {
  width: 0%;
  margin: 0;
  border: 0;
  height: 0.1vw;
  position: absolute;
  background-color: white;
  left: 8.3vw;
  top: 18vw;
}

.hi {
  border: 1px solid white;
  width: 13%;
  height: 10vh;
  border-radius: 1.6vw;
  background: #DBFEFE;
  z-index: 11;
  position: absolute;
  left: 8.3vw;
  bottom: 8vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.sayer {
  color: black;
  font-family: 'Quicksand', sans-serif;
  font-size: 1.6vw;
  font-weight: 600;
  margin-right: 2vw;
}

.wave {
  width: 5vw;
}

.intros {
  position: absolute;
  left: 8.3vw;
  top: 22vw;
}

.intros > div {
  color: white;
  font-family: 'Quicksand', sans-serif;
  font-size: 1.6vw;
  font-weight: lighter;
}

.bullet1 {
  width: 1.4vw;
  height: 1.4vw;
  border-radius: 100%;
  background: #DBFEFE;
  position: absolute;
  right: 30vw;
  top: 2vw;
  
}

.bullet2 {
  width: 1.4vw;
  height: 1.4vw;
  border-radius: 100%;
  background: #DBFEFE;
  position: absolute;
  right: 30vw;
  bottom: 2vw;
  
}