@media (max-width: 1024px) {
    .block1 {
        display: none;
        pointer-events: none;
    }
    .block2 {
        display: none;
        pointer-events: none;
    }
    .abss {
        display: none;
        pointer-events: none;
    }
    .visual {
        display: none;
        pointer-events: none;
    }
    .App {
        background: #2F2F2F;
    }
    .warn-block {
        display: block;
        border: 1px solid white;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        flex-direction: column;
        font-family: 'Lato', sans-serif;
    }

    .warn-block > h1 {
        color: white;
        text-align: center;
        font-size: 6vw;
        text-decoration: underline;
        padding-bottom: 5vw;
    }

    .warn-block > p {
        font-weight: lighter;
        color: rgb(247, 247, 247);
        margin-top: 1vw;
        font-size: 4vw;
    }
}